<template>
  <div class="estab">
    <div>
      <topBar></topBar>
    </div>
    <div class="estab_center">
      <p class="estab_wound"><span @click="create">创建小程序</span></p>
      <div class="estab_found">
        <div class="estab_tou" v-for="(item,index) in items" @mouseenter="cover(index)" @mouseleave="coverOut(index)">
          <div class="estab_left">
            <div class="estab_handimg" v-if="item.reve">
              <img :src="item.pic" />
            </div>
            <div class="estab_hand_img" v-if="!item.reve">
              <img src="../../assets/xingzhuang.png" />
              <button type="button" @click="seeData">数据统计</button>
            </div>
          </div>
          <div class="estab_right">
            <div>
              <span>{{item.name}}</span>
              <span>{{item.is_publish}}</span>
            </div>
            <div><span><!--{{item.two}}-->未生成二维码</span> /<span>更新于{{item.create_time}}前</span></div>
            <div class="editor_app">
              <span></span>
              <span @click="redact(item,index)">编辑</span>
              <span></span>
              <span @click="downLoad(item,index)">小程序下线</span>
            </div>
            <div class="buy_app">
              <span @click="buyProgram(index,item)">购买</span>
            </div>
            <div class="estab_abolish" v-if="!item.reve"><span @click="coverback(item,index)"></span></div>
          </div>
        </div>
      </div>
    </div>
    <div class="estab_purchase">
      <buy v-show="$store.state.purchase"></buy>
    </div>
    <Modal v-model="submit">
      <div slot="header" style="text-align:left">
        <!--<Icon type="ios-list"></Icon>-->
        <span>小程序</span>
      </div>
      <div style="text-align:center;" class="program_content">
        <h2 style="font-weight: 600">小程序数据助手</h2>
        <img src="../../assets/wmp-data-qrcode.jpg" alt="" style="width: 1.9rem;max-height: 100%">
      </div>
      <div slot="footer">
        <div style="display: flex;justify-content: center;align-items: center;padding: 0.1rem 0">
          使用小程序管理员扫码查看
        </div>
      </div>
    </Modal>

    <!--<div class="buy" v-show="dete">
      <div class="buy_img">
        <div @click="dateBack">关闭</div>
        <div class="buy_sure">
          <div><span></span><span>确认删除这个小程序吗？</span></div>
        </div>
        <div><span @click="dateSure">确认</span><span @click="dateBack">取消</span></div>
      </div>
    </div>-->
  </div>
</template>

<script>
  import buy from './buy.vue'
  import topBar from './topBar.vue'
  export default {
    data() {
      return {
        msg: "123",
        submit: false,
        items: [],
        reveal: false,
        reve: true,
        issue: true,
        dete: false,
        oldTime: '',
        id: '',
        modal1: false,
        wallet: {},
      }
    },
    methods: {
//        下线
      downLoad(item,index){
        if(item.is_publish === '已上线'){
          this.$Modal.confirm({
            title: '下线',
            content: '<p>确定下线这个小程序</p>',
            onOk: () => {
              this.post('custom/Own/update_app_state',{
                appid: item.appid,
                is_publish: 5
              }).then(res => {
                if (res.data.code === 10000){
                  this.$Modal.success({
                    title: "成功下线小程序",
                    content: res.data.msg
                  });
                  this.getUserApplist();
                }else{
                  this.$Modal.error({
                    title: "小程序下线失败",
                    content: res.data.msg
                  });
                }
              })
            },
            onCancel: () => {

            }
          });
        }else{
          this.$Modal.error({
            title: "小程序下线失败",
            content: "小程序还未上线,不能进行该操作"
          });
        }
      },
      seeData() {
        this.submit = true;
      },
      redact(item, index) {
        this.seeAppForbidden(item.appid).then(val => {
              if (val === 1) {
                this.$Modal.error({
                  title: "禁用通知",
                  content: "小程序已被禁用,请联系管理员"
                });
              } else {
                if(item.type === 1) {
                  if(item.use_time * 1000 > new Date().getTime()) {
                    sessionStorage.setItem('appid', item.appid);
                    let url = `/${item.appid}` + '/editor';
                    this.$router.push(url);
                  } else {
                    this.$Modal.error({
                      title: "错误信息",
                      content: "小程序已过期,请重新进行购买"
                    });
                  }
                } else if(item.type === 2) {
                  if(item.use_time * 1000 > new Date().getTime()) {
                    sessionStorage.setItem('appid', item.appid);
                    let url = `/${item.appid}` + '/orderIndex';
                    this.$router.push(url);
                  } else {
                    this.$Modal.error({
                      title: "错误信息",
                      content: "小程序已过期,请重新进行购买"
                    });
                  }
                } else if(item.type === 3) {
                  if(item.use_time * 1000 > new Date().getTime()) {
                    sessionStorage.setItem('appid', item.appid);
                    let url = `/${item.appid}` + '/hotel';
                    this.$router.push(url);
                  } else {
                    this.$Modal.error({
                      title: "错误信息",
                      content: "小程序已过期,请重新进行购买"
                    });
                  }
                }
              }
          });
      },
//      查看小程序是否被禁用
      seeAppForbidden (appid) {
       return this.post('custom/Own/getAppInfo',{
            appid
        }).then(res => {
          if (res.data.code === 10000) {
            return res.data.data.is_forbidden;
          } else{
            this.$Modal.error({
              title: "获取失败",
              content: res.data.msg
            });
          }
        })
      },
      //      删除
      coverback(item, index) {
        this.$Modal.confirm({
          title: '删除',
          content: '<p>确定删除这个小程序</p>',
          onOk: () => {
            this.post("custom/Own/delUserApp", {
              appid: item.appid
            }).then(res => {
              if(res.data.code === 10000) {
                this.$Modal.success({
                  title: "删除成功",
                  content: res.data.msg
                });
                this.items.splice(index, 1);
              } else {
                const content = res.data.msg_test + "请重新删除";
                this.$Modal.error({
                  title: "删除失败",
                  content: content
                });
              }
            })
          },
          onCancel: () => {

          }
        });
      },
      dateBack() {
        this.dete = false
      },
      create() {
        this.$router.push('/createApp');
      },
      cover(index) {
        this.items[index].reve = false;
        this.items.splice(index, 1, this.items[index])
      },
      coverOut(index) {
        this.items[index].reve = true;
        this.items.splice(index, 1, this.items[index])
      },
      buyProgram(index, item) {
        this.$router.push(`/payment/${item.appid}`)
      },
//      获取小程序列表
      getUserApplist(){
        this.post('custom/Own/getUserAppList', {}).then(res => {
          if(res.data.code === 10000) {
            this.$Spin.hide();
            this.items = res.data.data;
            this.items.forEach(item => {
              item.pic = "http://weapp.xiguawenhua.com/" + item.pic;
            });
            for(let i in this.items) {
              this.items[i].reveal = false;
              this.items[i].reve = true;
              if(this.items[i].is_publish === 0) {
                this.items[i].is_publish = "未发布"
              } else if(this.items[i].is_publish === 1){
                this.items[i].is_publish = "已绑定"
              }else if(this.items[i].is_publish === 3){
                this.items[i].is_publish = "审核中"
              }else if(this.items[i].is_publish === 4){
                this.items[i].is_publish = "已上线"
              }else if(this.items[i].is_publish === 5){
                this.items[i].is_publish = "已下线"
              }
              let time = new Date().getTime() - (this.items[i].create_time * 1000);
              if(this.items[i].create_time > 24) {
                this.items[i].create_time = Math.round(time / (1000 * 3600 * 24)) + "天"
              } else {
                this.items[i].create_time = Math.round(time / (1000 * 3600)) + "小时"
              }
            }
          } else {
            this.$router.push('/')
          }
        })
      }
    },
    created() {

    },
    mounted() {
      this.getUserApplist();
    },
    components: {
      buy,
      topBar
    }
  }
</script>
<style scoped="scoped">
