    margin-top: 0.15rem;
    border: 1px solid #FFFFFF;
    background: none;
    color: #FFFFFF;
    cursor: pointer;
    border-radius: 0.017rem;
  }

  .estab_mask {
    width: 50%;
    position: absolute;
