    display: flex;
    flex-wrap: wrap;
    margin-top: 0.3rem;
  }

  .estab_right>div:nth-child(1) {
    margin-top: 0.4rem;
    margin-left: 0.24rem;
    margin-bottom: 0.04rem;
  }

  .estab_right>div:nth-child(1)>span:nth-child(1) {
    width: 1.68rem;
