<template>
	<div class="buy">
		<div class="buy_img">
			<p @click="goBack">关闭</p>
			<div class="buy_sure">
				<p><span></span><span>确认购买这个小程序吗？</span></p>
			</div>
			<p><span @click="goSure">确认</span><span @click="goBack">取消</span></p>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {}
		},
		methods: {
			goBack() {
				this.$store.state.purchase = false
			},
			goSure() {
				this.post('custom/Own/buyUserApp', {
					appid: this.$store.state.appid
				}).then(res => {
					if(res.data.code === 10000) {
						const content = res.data.msg
						this.$Modal.success({
                  title: "购买成功",
                  content: content
              });
					} else{
						const content = res.data.msg
                this.$Modal.error({
                title: "购买失败",
                 content: content
            });
					}
				})
			}
		},
	}
</script>

<style scoped="scoped">
	.buy {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba(0, 0, 0, 0.2);
		display: flex;
		justify-content: center;
	}

	.buy_img {
		margin-top: 2rem;
		width: 4rem;
		height: 2.2rem;
		background: white;
		border-radius: 0.04rem;
	}

	.buy_img>p:nth-child(1) {
		position: relative;
		left: 90%;
		top: 10%;
		cursor: pointer;
		width: 0.4rem;
		height: 0.3rem;
	}

	.buy_sure {
		margin: 0.4rem 0.3rem;
	}

	.buy_sure>p>span:nth-child(1) {
		display: inline-block;
		width: 0.4rem;
		height: 0.4rem;
		background: url(../../assets/15.jpg) no-repeat;
		background-size: 100%;
	}

	.buy_sure>p>span:nth-child(2) {
		display: inline-block;
		position: relative;
		top: -0.1rem;
		left: ;
		font-size: 0.14rem;
	}

	.buy_img>p:nth-child(3) {
		margin: 0.4rem 0.3rem;
		display: flex;
		flex-direction: row-reverse;
	}

	.buy_img>p:nth-child(3)>span {
		display: block;
		width: 0.5rem;
		height: 0.3rem;
		text-align: center;
		line-height: 0.3rem;
		background: #89D04A;
		margin-right: 0.2rem;
		color: #F3F3F3;
		border-radius: 0.04rem;
	}
</style>



